import displayInfoContainer from "../components/infoContainer";
import StoreUser from "../entities/StoreUser";
import returnHome from "../helpers/returnHome";
import signUpView from "../views/signUpView";
export default function SignUpViewLogic(store, mainContent) {
    //rendering the view
    mainContent.innerHTML = signUpView();
    //sign up form functionality
    var signUpForm = document.getElementById("sign-up-form");
    signUpForm["submit-button"].addEventListener("click", function (event) {
        event.preventDefault();
        try {
            var newUser_1 = new StoreUser();
            newUser_1.firstName = signUpForm["first-name"].value;
            newUser_1.lastName = signUpForm["last-name"].value;
            newUser_1.mailAdress = signUpForm["mail-adress"].value;
            if (signUpForm["password"].value != signUpForm["password-repeat"].value) {
                throw Error("El campo 'Contraseña' y 'Repetir contraseña' no coinciden.");
            }
            else {
                newUser_1.password = signUpForm["password"].value;
            }
            //add user to the store
            store.user = newUser_1;
            //add user name to the page
            var userName = document.querySelectorAll(".js-user");
            userName.forEach(function (element) {
                element.innerHTML = "<i class=\"fas fa-user\"></i> &nbsp " + newUser_1.firstName.toUpperCase();
            });
            //remove privileges
            var navBarContainer = document.getElementById("nav-bar-container");
            var productCreationLink = document.getElementById("product-creation-link");
            if (productCreationLink) {
                navBarContainer.removeChild(productCreationLink);
            }
            //return home and show info container success
            displayInfoContainer("El usuario ha sido creado correctamente.");
            returnHome();
        }
        catch (error) {
            //show info container error
            displayInfoContainer("" + error);
        }
    });
}
