import Cart from "./Cart";
import Product from "./Product";
import StoreUser from "./StoreUser";
import products from "../helpers/products";
var Store = /** @class */ (function () {
    function Store() {
        this._catalog = [];
        this._cart = new Cart();
    }
    Object.defineProperty(Store.prototype, "user", {
        get: function () {
            return this._user;
        },
        set: function (user) {
            this._user = user;
            this.saveUser();
        },
        enumerable: false,
        configurable: true
    });
    Store.prototype.loadUser = function () {
        var loadData = localStorage.getItem("user");
        if (loadData != null) {
            var user = new StoreUser();
            user.create(JSON.parse(loadData));
            this._user = user;
        }
    };
    Store.prototype.clearUser = function () {
        this._user = undefined;
        localStorage.removeItem("user");
    };
    Store.prototype.saveUser = function () {
        localStorage.setItem("user", JSON.stringify(this._user));
    };
    Store.prototype.loadCatalog = function () {
        var _this = this;
        var loadData = localStorage.getItem("products");
        var loadDataJson;
        if (loadData != null) {
            loadDataJson = JSON.parse(loadData);
        }
        else {
            localStorage.setItem("products", JSON.stringify(products()));
            loadData = localStorage.getItem("products");
            loadDataJson = JSON.parse(loadData);
        }
        loadDataJson.forEach(function (element) {
            var prod = new Product();
            prod.create(element);
            _this._catalog.push(prod);
        });
    };
    Store.prototype.showCatalog = function () {
        return this._catalog;
    };
    Store.prototype.newProduct = function (prod) {
        this._catalog.push(prod);
        localStorage.setItem("products", JSON.stringify(this._catalog));
    };
    Store.prototype.getProductById = function (id) {
        var product = this.showCatalog().filter(function (prod) { return prod.id == id; });
        return product;
    };
    Store.prototype.removeProduct = function (id) {
        var productSelected = this._catalog.findIndex(function (p) { return p.id == id; });
        this._catalog.splice(productSelected, 1);
        localStorage.setItem("products", JSON.stringify(this._catalog));
    };
    Object.defineProperty(Store.prototype, "cart", {
        get: function () {
            return this._cart;
        },
        enumerable: false,
        configurable: true
    });
    return Store;
}());
export default Store;
