export default function newProductView() {
    return "\n    <div class=\"new-product-container\" id=\"main-content\">\n      <div class=\"new-product-title\">\n        <h1>Crear nuevo producto</h1>\n      </div>\n\n      <div class=\"new-product-content\">\n        <form class=\"new-product-form\" id=\"new-product-form\">\n          <div class=\"form-text-input-container\">\n            <label>Nombre:</label>\n            <input\n              type=\"text\"\n              placeholder=\"Nombre del producto\"\n              class=\"form-text-input\"\n              name=\"product-name\"\n            />\n          </div>\n\n          <div class=\"form-text-input-container\">\n            <label>Tipo:</label>\n            <select class=\"form-text-input\" name=\"product-type\" id=\"type-selector\">\n              <!-- javascript rendered -->\n            </select>\n          </div>\n\n          <div class=\"form-text-input-container\">\n            <label>Precio:</label>\n            <input\n              type=\"number\"\n              step=\"0.01\"\n              min=\"0.00\"\n              value=\"0\"\n              class=\"form-text-input\"\n              name=\"product-price\"\n              placeholder=\"Precio del producto\"\n            />\n          </div>\n\n          <div class=\"form-text-input-container\">\n            <label>Descripci\u00F3n:</label>\n            <input\n              type=\"text\"\n              class=\"form-text-input\"\n              name=\"product-description\"\n              placeholder=\"Descripcion del producto\"\n            />\n          </div>\n\n          <div class=\"form-text-input-container\">\n            <label>Imagen:</label>\n            <input\n              type=\"text\"\n              class=\"form-text-input\"\n              name=\"product-image\"\n              placeholder=\"URL de la imagen del producto\"\n            />\n          </div>\n\n          <div class=\"form-text-input-container\">\n            <label>Imagen tipo thumbnail:</label>\n            <input\n              type=\"text\"\n              class=\"form-text-input\"\n              name=\"product-thumbnail\"\n              placeholder=\"URL de la imagen thumbnail del producto\"\n            />\n          </div>\n\n          <div class=\"form-text-input-container\">\n            <label>Stock:</label>\n            <input\n              type=\"number\"\n              step=\"1\"\n              min=\"0\"\n              value=\"0\"\n              placeholder=\"Unidades de stock disponibles\"\n              class=\"form-text-input\"\n              name=\"product-stock\"\n            />\n          </div>\n\n          <div class=\"form-text-input-container\">\n            <label>Descuento:</label>\n            <input\n              type=\"number\"\n              step=\"0.001\"\n              min=\"0.00\"\n              value=\"0\"\n              class=\"form-text-input\"\n              name=\"product-discount\"\n              placeholder=\"Descuento del producto en %\"\n            />\n          </div>\n\n          <div class=\"form-button-container\">\n            <input\n              type=\"submit\"\n              value=\"Crear Producto\"\n              class=\"form-button\"\n              id=\"new-product-submit-button\"\n            />\n          </div>\n        </form>\n      </div>\n    </div>\n";
}
